body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  width: 50vw;
  height: 50vh;
  /* display: grid; */
  justify-content: center;
  align-content: center;
  justify-items: center;
  align-items: center;
  text-align: center;
  /* grid-template-columns: repeat(3, auto); */
}
.card {
  background: #fff;
  border-radius: 2px;
  margin: 1rem;
  position: relative;
  width: 100%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  transition: -webkit-transform .25s;
  transition: transform .25s;
  transition: transform .25s, -webkit-transform .25s;
}

.card > .img-container {
  height: 80%;
  overflow: hidden;
  text-align: center;
  background:white;
}
.card > .img-container > img {
  width: 100%;
  cursor: pointer;
}

.card:hover {
  -webkit-transform: scale(1.0625) translateY(-2px);
          transform: scale(1.0625) translateY(-2px);
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > #projectTitle {
  list-style-type: none;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 1rem;
  text-decoration: underline;
  color: #2A77AE;
  margin-bottom: 50px;
}

button.up-vote, button.down-vote{
  height: 30px;
  width: 30px;
  margin-right: 5px;
}




#title {
  display: inline-block;
  margin-top: 30px;
  color: white;
}

/* up and down vote buttons */
button.up-vote,
button.down-vote {
  height: 30px;
  width: 30px;
  margin-right: 5px;
}

#big-idea {
  font-family: "Open Sans Condensed", sans-serif;
  height: 75px;
  font-size: 25px;
  color: white;
  margin-top: 20px;
  border-style: solid;
  border-color: white;
  border-width: 0px 0px 2px 0px;
}
::-webkit-input-placeholder {
  font-size: 100px;
}
::-ms-input-placeholder {
  font-size: 100px;
}
::placeholder {
  font-size: 100px;
}

.form-control::-webkit-input-placeholder {
  color: white;
} /* WebKit, Blink, Edge */
.form-control:-moz-placeholder {
  color: white;
} /* Mozilla Firefox 4 to 18 */
.form-control::-moz-placeholder {
  color: white;
} /* Mozilla Firefox 19+ */
.form-control:-ms-input-placeholder {
  color: white;
} /* Internet Explorer 10-11 */
.form-control::-ms-input-placeholder {
  color: white;
}

.list-group-item {
  line-height: 1;
}
button.comments {
  float: right;
}
.pitch-item {
  cursor: pointer;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1 {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav-Links {
    width: 50%;
}
